"use client";

import { useEffect, useState } from "react";

/* ===============================
 * ÎÇ†Ïßú Ïú†Ìã∏
 * =============================== */

function formatDate(date) {
  return date.toISOString().slice(0, 10);
}

function getMonthMatrix(year, month) {
  const firstDay = new Date(year, month, 1);
  const lastDay = new Date(year, month + 1, 0);

  const start = new Date(firstDay);
  start.setDate(start.getDate() - ((start.getDay() + 6) % 7)); // ÏõîÏöîÏùº ÏãúÏûë

  const end = new Date(lastDay);
  end.setDate(end.getDate() + (7 - ((end.getDay() + 6) % 7) - 1));

  const days = [];
  const cur = new Date(start);

  while (cur <= end) {
    days.push(new Date(cur));
    cur.setDate(cur.getDate() + 1);
  }

  return days;
}

/* ===============================
 * Page
 * =============================== */

export default function Home() {
  const today = new Date();
  const [year, setYear] = useState(today.getFullYear());
  const [month, setMonth] = useState(today.getMonth()); // 0-based
  const [banners, setBanners] = useState([]);

  useEffect(() => {
    fetch("http://localhost:3000/api/homebanner")
      .then(res => res.json())
      .then(setBanners)
      .catch(console.error);
  }, []);

  const days = getMonthMatrix(year, month);

  function changeMonth(diff) {
    const d = new Date(year, month + diff, 1);
    setYear(d.getFullYear());
    setMonth(d.getMonth());
  }

  return (
    <div className="min-h-screen bg-zinc-50 px-6 py-8 dark:bg-black">
      <div className="mx-auto max-w-6xl">
        {/* Ìó§Îçî */}
        <div className="mb-6 flex items-center justify-between">
          <h1 className="text-2xl font-semibold text-zinc-900 dark:text-zinc-100">
            üìÜ ÏõîÍ∞Ñ Î∞∞ÎÑà ÏùºÏ†ï
          </h1>

          <div className="flex items-center gap-3">
            <button
              onClick={() => changeMonth(-1)}
              className="rounded px-3 py-1 text-sm hover:bg-zinc-200 dark:hover:bg-zinc-800"
            >
              ‚óÄ
            </button>
            <span className="font-medium text-zinc-700 dark:text-zinc-300">
              {year}.{String(month + 1).padStart(2, "0")}
            </span>
            <button
              onClick={() => changeMonth(1)}
              className="rounded px-3 py-1 text-sm hover:bg-zinc-200 dark:hover:bg-zinc-800"
            >
              ‚ñ∂
            </button>
          </div>
        </div>

        {/* ÏöîÏùº Ìó§Îçî */}
        <div className="grid grid-cols-7 border border-zinc-200 text-center text-sm font-medium dark:border-zinc-800">
          {["Ïõî", "Ìôî", "Ïàò", "Î™©", "Í∏à", "ÌÜ†", "Ïùº"].map(d => (
            <div
              key={d}
              className="border-b border-zinc-200 bg-zinc-100 py-2 dark:border-zinc-800 dark:bg-zinc-900"
            >
              {d}
            </div>
          ))}
        </div>

        {/* Ï∫òÎ¶∞Îçî */}
        <div className="grid grid-cols-7 border border-zinc-200 dark:border-zinc-800">
          {days.map(date => {
            const dateStr = formatDate(date);
            const isCurrentMonth = date.getMonth() === month;

            const dayBanners = banners.filter(
              b => b.startDate <= dateStr && b.endDate >= dateStr
            );

            return (
              <div
                key={dateStr}
                className={`min-h-[120px] border border-zinc-200 p-2 text-sm dark:border-zinc-800
                  ${isCurrentMonth ? "" : "bg-zinc-100 dark:bg-zinc-900 text-zinc-400"}
                `}
              >
                <div className="mb-1 text-xs font-medium">
                  {date.getDate()}
                </div>

                <div className="space-y-1">
                  {dayBanners.map(item => (
                    <div
                      key={item.id}
                      className="truncate rounded bg-blue-100 px-1 py-0.5 text-xs text-blue-800 dark:bg-blue-900 dark:text-blue-200"
                      title={`${item.banner} (${item.department})`}
                    >
                      {item.banner}
                    </div>
                  ))}
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}
